<template>
  <ld-modal
    ref="modal"
    :title="'Are you ABSOLUTELY sure?'"
    :persistent="isCancelDisabled"
    @close="onModalClose"
  >
    <div
      class="slds-p-around_medium delete-account-modal"
      slot="content"
    >
      <div class="slds-text-body_regular">
        This action CANNOT be undone. This will permanently delete the <strong>{{ configuration.Name }}</strong> WeChat account and all relevant data.
      </div>
      <div class="slds-form-element delete-account-value">
        <label class="slds-form-element__label" for="delete-account-label">Please type in the name of the WeChat account to confirm.</label>
        <div class="slds-form-element__control">
          <input
            id="delete-account-label"
            class="slds-input"
            type="text"
            v-model="deleteConfigurationName"
          />
        </div>
      </div>
    </div>
    <footer
      slot="footer"
    >
      <ld-button
        neutral
        :disabled="isCancelDisabled"
        @click="close()"
      >
        Cancel
      </ld-button>
      <ld-button
        brand
        :disabled="isConfirmDisabled"
        @click="confirmDelete()"
      >
        Confirm
      </ld-button>
    </footer>
  </ld-modal>
</template>
<script>
import { WeChatAccountConfigurationService } from '../../service'
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdButton } from 'charket-ui/dist/ld-button.js'

export default {
  components: {
    LdModal,
    LdButton
  },
  computed: {
    isConfirmDisabled () {
      return this.configuration.Name !== this.deleteConfigurationName || this.isDeleting
    },
    isCancelDisabled () {
      return this.isDeleting
    }
  },
  data () {
    return {
      deleteConfigurationName: '',
      isDeleting: false,
      configuration: {}
    }
  },
  methods: {
    open (configuration) {
      this.configuration = configuration
      this.$refs.modal.open()
    },
    close () {
      this.$refs.modal.close()
    },
    async confirmDelete () {
      this.isDeleting = true
      try {
        await WeChatAccountConfigurationService.delete(this.configuration.Id)
        this.emitSuccess()
      } catch (error) {
        const errorMessage = this.$parseErrorMessage(error)
        this.$refs.modal.showError(errorMessage)
      } finally {
        this.isDeleting = false
        this.close()
      }
    },
    emitSuccess () {
      this.$bus.emit('success', { content: 'Your request was submitted. You will receive an email once the deletion of the WeChat account and all its related data is completed.', type: 'success' })
      this.$emit('success')
      this.close()
    },
    onModalClose () {
      this.deleteConfigurationName = ''
    }
  }
}
</script>
<style lang="scss" scoped>
.delete-account-modal {
  text-align: start;
  padding: 1rem;
  align-items: start;
}
.delete-account-value {
  margin-top: 1rem;
  width: 100%;
}
</style>
