<template>
  <ld-modal
    class="activate-modal"
    ref="Modal"
    title="Activate Connection"
    :width="640"
    @close="onModalClose()"
  >
    <div
      class="scroll-content slds-scrollable"
      :class="{'modal-content': !isLoading}"
      slot="content"
    >
      <div
        class="activate-modal__loading"
        v-show="isLoading"
      >
        <ld-spinner color="brand"></ld-spinner>
      </div>

      <div
        class="activate-modal__content"
        v-if="isFirstStep && !isLoading"
      >
        <modal-toast
          class="slds-m-bottom_medium"
          v-if="showWarningToast"
          type="warning"
        >
          <div>
            Some WeChat data, usually the WeChat menu and auto replies, failed to be synced to Salesforce. Click
            <a class="activate-modal__link" :href="charketAccountHomeUrl" target="_blank">here</a>
             to correct the data before activating this WeChat connection.
          </div>
        </modal-toast>
        <section class="activate-modal__info">
          <p class="title">Activate WeChat Connection</p>
          <p class="description">The connection with WeChat needs to be activated by the WeChat admin by following the steps below.</p>
        </section>
        <ul class="activate-modal__steps">
          <li>1. Log into the <a href="https://mp.weixin.qq.com" target="_blank"> WeChat admin portal.</a></li>
          <li>2. Go to <b></b> and click <b></b>.</li>
          <li>3. Click <b></b> under the <b></b> section.</li>
        </ul>
      </div>

      <div v-if="isSecondStep && !isLoading">
        <modal-toast
          class="slds-m-bottom_medium"
          v-if="isSetupDone"
          type="success"
        >
          <div>The connection was activated successfully.</div>
        </modal-toast>
        <section class="activate-modal__info">
          <p class="title">Verify Connection</p>
          <p>{{ verifyTip }}</p>
        </section>
        <div class="slds-grid slds-grid_align-center">
          <image-wrapper
            class="activate-modal__qrcode"
            v-if="showQrcode"
            :image="`/servlet/servlet.FileDownload?file=${attachmentId}`"
          ></image-wrapper>
        </div>
      </div>
    </div>

    <div slot="footer">
      <ld-button
        brand
        v-if="isFirstStep"
        @click="onClickNext()"
      >
        Next
      </ld-button>
      <template v-if="isSecondStep">
        <ld-button
          brand
          disabled
          v-if="!isSetupDone"
        >
          Checking
        </ld-button>
        <ld-button
          brand
          v-else
          @click="onClickDone()"
        >
          Done
        </ld-button>
      </template>
    </div>
  </ld-modal>
</template>

<script>
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdButton } from 'charket-ui/dist/ld-button.js'
import { LdSpinner } from 'charket-ui/dist/ld-spinner.js'
import { ImageWrapper } from 'charket-ui/dist/image-wrapper.js'
import { WeChatAccountConfigurationService } from '@/service'
import { SFEnvironment } from '../../../lib'

export default {
  name: 'ActivateModal',
  components: {
    LdModal,
    LdButton,
    LdSpinner,
    ImageWrapper
  },
  props: {
    configurationId: {
      type: String,
      default () {
        return ''
      }
    },
    wechatAccountType: {
      type: String,
      default () {
        return ''
      }
    }
  },
  data () {
    return {
      step: 1,
      isLoading: false,
      checkTimer: null,
      showWarningToast: false,
      attachmentId: '',
      isSetupDone: false
    }
  },
  computed: {
    isFirstStep () {
      return this.step === 1
    },
    isSecondStep () {
      return this.step === 2
    },
    charketAccountHomeUrl () {
      const namespace = SFEnvironment.charketNamespace
        ? `${SFEnvironment.charketNamespace}__`
        : ''
      return `/apex/${namespace}CharketApp#/${this.configurationId}/home`
    },
    verifyTip () {
      if (this.wechatAccountType === 'Service Account') {
        return 'Verify if the connection has been successfully activated by scanning the QR code below using WeChat.'
      } else {
        return this.attachmentId
          ? 'Verify if the connection has been successfully activated by scanning the QR code below using WeChat and sending to this subscription account a message.'
          : 'Verify if the connection has been successfully activated by sending this subscription account a WeChat message.'
      }
    },
    showQrcode () {
      return this.attachmentId || this.hasError
    }
  },
  methods: {
    async open () {
      this.isLoading = true
      this.step = 1
      this.showWarningToast = false
      this.$refs.Modal.open()
      try {
        const { autoReply, menu } = await WeChatAccountConfigurationService.getIntegrationStatus(this.configurationId)
        this.showWarningToast = !autoReply || !menu
      } catch (error) {
        const errorMessage = this.$parseErrorMessage(error)
        this.$refs.Modal.showError(errorMessage)
      } finally {
        this.isLoading = false
      }
    },
    onClickDone () {
      this.$refs.Modal.close()
      this.$emit('setup-done')
      clearInterval(this.checkTimer)
    },
    onModalClose () {
      this.$emit('close', { changed: true })
      clearInterval(this.checkTimer)
    },
    async onClickNext () {
      this.isLoading = true
      this.step = 2
      this.isSetupDone = false
      this.attachmentId = ''
      this.hasError = false
      try {
        this.attachmentId = await WeChatAccountConfigurationService.getActivatedQRCodeAttachmentId(this.configurationId)
        this.checkTimer = setInterval(() => {
          this.checkSetup()
        }, 10000)
      } catch (error) {
        this.hasError = true
        const errorMessage = this.$parseErrorMessage(error)
        this.$refs.Modal.showError(errorMessage)
      } finally {
        this.isLoading = false
      }
    },
    async checkSetup () {
      try {
        this.isSetupDone = await WeChatAccountConfigurationService.isSetupDone(this.configurationId)
      } catch (error) {
        const errorMessage = this.$parseErrorMessage(error)
        this.$refs.Modal.showError(errorMessage)
      }
    }
  }
}
</script>

