  .activate-modal {
    .scroll-content {
      height: 100%;
    }
    .modal-content {
      padding: 16px 16px 40px;
    }
    &__loading {
      position: relative;
      padding: 32px;
    }

    &__info {
      padding-top: 16px;
      text-align: center;
      .title {
        font-size: 24px;
        font-weight: bold;
        line-height: 24px;
        margin-bottom: 16px;
      }
      .description {
        padding: 0 75px;
      }
    }

    &__steps {
      margin-top: 30px;
      margin-left: 18px;
      li {
        line-height: 20px;
        margin-top: 12px;
      }
    }

    &__link {
      color: #080707;
      text-decoration: underline;
    }

    &__qrcode {
      margin-top: 30px;
      width: 204px;
      height: 204px;
    }
  }
</style>
