<template>
  <ld-modal
    class="progress-modal"
    ref="Modal"
    title="Link WeChat Account"
    width="640px"
    @close="onModalClose()"
  >
    <div
      class="scroll-content slds-scrollable"
      slot="content"
    >
      <div class="slds-progress slds-progress_vertical">
        <ol class="slds-progress__list">
          <li
            class="slds-progress__item"
            v-for="(step, index) in steps"
            :key="index"
            :class="{
              'slds-is-completed': getStepStatus(step.Order) === 'Completed',
              'slds-is-active': getStepStatus(step.Order) === 'Active',
              'slds-has-error': getStepStatus(step.Order) === 'Error'
            }"
          >
            <div
              class="slds-progress__marker slds-progress__marker_icon slds-icon_container slds-icon-utility-error"
              v-if="getStepStatus(step.Order) === 'Error'"
            >
              <ld-tooltip>
                <p>{{ errorMessage }}</p>
                <template slot="activator">
                  <icon
                    icon-class-name="slds-icon slds-icon_xx-small"
                    name="error"
                  ></icon>
                </template>
              </ld-tooltip>
            </div>
            <div
              class="slds-progress__marker slds-progress__marker_icon slds-icon_container slds-icon-utility-success"
              v-else-if="getStepStatus(step.Order) === 'Completed'"
            >
              <icon
                icon-class-name="slds-icon slds-icon_xx-small"
                name="success"
              ></icon>
            </div>
            <div
              class="slds-progress__marker"
              v-else
            ></div>
            <div class="slds-progress__item_content slds-grid slds-grid_align-spread slds-border_bottom progress-item">
              <div>
                <div class="progress-item__title">{{ step.Title }}</div>
                <div>{{ step.Description }}</div>
              </div>
              <button
                class="slds-button slds-button_neutral progress-item__button"
                :disabled="checkDisabled(step.Order)"
                @click="onClickStep(step.Order)"
              >
                {{ step.ButtonName }}
              </button>
            </div>
          </li>
        </ol>
      </div>

      <salesforce-setup
        ref="SalesforceSetup"
        @create="onConfigurationCreated"
        @setup-done="onSetupDone"
        @close="onClose"
        @change="onChange()"
        @link-method-changed="onLinkMethodChanged"
      ></salesforce-setup>

      <official-setup-modal
        ref="OfficialSetupModal"
        :configuration-id="configurationId"
        @setup-done="onSetupDone"
        @close="onClose"
      ></official-setup-modal>

      <activate-modal
        ref="ActivateModal"
        :configuration-id="configurationId"
        :wechat-account-type="wechatAccountType"
        @setup-done="onSetupDone"
        @close="onClose"
      ></activate-modal>

      <sync-data-modal
        ref="syncDataModal"
        @setup-done="onSetupDone"
        @close="onSyncDataModalClose"
        @change="onChange"
      ></sync-data-modal>
    </div>

    <div slot="footer">
      <button
        class="slds-button slds-button_neutral"
        @click="close()"
      >
        Close
      </button>
    </div>
  </ld-modal>
</template>

<script>
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdSpinner } from 'charket-ui/dist/ld-spinner.js'
import { LdTooltip } from 'charket-ui/dist/ld-tooltip.js'
import SalesforceSetup from './salesforce_setup'
import OfficialSetupModal from './OfficialSetupModal.vue'
import SyncDataModal from './SyncDataModal.vue'
import ActivateModal from './ActivateModal.vue'
import { WeChatAccountConfigurationService } from '../../../service'

const stepTitle = [
  {
    'Direct Integration': 'Set up Salesforce',
    'Charket Social Hub': 'Set up Salesforce'
  },
  {
    'Direct Integration': 'Set up WeChat',
    'Charket Social Hub': 'Set up Charket Social Hub'
  }
]
const stepDescription = [
  {
    'Direct Integration': 'WeChat settings from your WeChat admin are needed in order to complete this step.',
    'Charket Social Hub': 'Contact the Charket support team to get the Social Hub connection information.'
  },
  {
    'Direct Integration': 'Go to the WeChat admin portal to complete the WeChat end setup.',
    'Charket Social Hub': 'Inform the Charket support team to complete the setup within the Social Hub.'
  }
]

export default {
  name: 'ProgressModal',
  components: {
    LdModal,
    LdSpinner,
    LdTooltip,
    SalesforceSetup,
    OfficialSetupModal,
    ActivateModal,
    SyncDataModal
  },
  props: {
    type: {
      type: String,
      default () {
        return ''
      }
    },
    linkMethod: {
      type: String,
      default () {
        return ''
      }
    }
  },
  data () {
    return {
      changed: false,
      currentSetupStep: 1,
      progressTimer: null,
      wechatAccountType: '',
      configurationId: '',
      configuration: null,
      stopTimer: false,
      errorInfo: null
    }
  },
  computed: {
    salesforceSetupButtonLabel () {
      return this.configurationId ? 'Edit' : 'Start'
    },
    step2ButtonLabel () {
      return this.linkMethod === 'Charket Social Hub' ? 'Contact' : 'Learn How'
    },
    steps () {
      const steps = [
        {
          Order: 1,
          Title: this.linkMethod ? stepTitle[0][this.linkMethod] : '',
          Description: this.linkMethod ? stepDescription[0][this.linkMethod] : '',
          ButtonName: this.salesforceSetupButtonLabel
        },
        {
          Order: 2,
          Title: this.linkMethod ? stepTitle[1][this.linkMethod] : '',
          Description: this.linkMethod ? stepDescription[1][this.linkMethod] : '',
          ButtonName: this.step2ButtonLabel
        }
      ]

      if (this.wechatAccountType !== 'Mini Program') {
        steps.push({
          Order: 3,
          Title: 'Sync WeChat Data',
          Description: 'Sync WeChat data into Salesforce.',
          ButtonName: 'Sync Data'
        },
        {
          Order: 4,
          Title: 'Activate Connection',
          Description: 'Activate the connection in the WeChat admin portal.',
          ButtonName: 'Activate'
        })
      }

      return steps
    },
    errorMessage () {
      return this.errorInfo
        ? this.errorInfo.Message
        : ''
    }
  },
  watch: {
    type (newValue) {
      this.wechatAccountType = newValue
    }
  },
  methods: {
    async getCurrentStep () {
      if (!this.configurationId || this.stopTimer) {
        return
      }
      try {
        this.currentSetupStep = await WeChatAccountConfigurationService.getCurrentStep(this.configurationId)
        this.errorInfo = null
      } catch (error) {
        if (error.name === 'RemoteAPIError') {
          this.errorInfo = {
            CurrentSetupStep: error.data.CurrentSetupStep,
            Message: error.data.Message
          }
        }
      }
    },
    getStepStatus (order) {
      if (this.errorInfo && this.errorInfo.CurrentSetupStep === order) {
        return 'Error'
      }
      if (this.currentSetupStep === -1) {
        return 'Completed'
      }
      if (order < this.currentSetupStep) {
        return 'Completed'
      } else if (order === this.currentSetupStep) {
        return 'Active'
      } else {
        return ''
      }
    },
    open (configuration) {
      this.configuration = configuration
      this.currentSetupStep = configuration && configuration.ProgressInfo
        ? configuration.ProgressInfo.CurrentSetupStep
        : 1
      this.configurationId = configuration
        ? configuration.Id
        : ''
      this.wechatAccountType = configuration
        ? configuration.Type
        : this.wechatAccountType
      this.changed = false
      this.$refs.Modal.open()
      this.getCurrentStep()
      this.progressTimer = setInterval(() => {
        this.getCurrentStep()
      }, 30000)
    },
    onChange () {
      this.changed = true
    },
    close () {
      this.errorInfo = null
      if (!this.changed && this.configuration && this.configuration.ProgressInfo) {
        this.changed = this.changed || this.configuration.ProgressInfo.CurrentSetupStep !== this.currentSetupStep
      }
      clearInterval(this.progressTimer)
      this.$refs.Modal.close()
    },
    checkDisabled (order) {
      return this.getStepStatus(order) !== 'Completed' &&
        this.getStepStatus(order) !== 'Active' &&
        this.getStepStatus(order) !== 'Error'
    },
    onClickStep (order) {
      this.stopTimer = true
      if (order === 1) {
        this.$refs.SalesforceSetup.open({
          configurationId: this.configurationId,
          wechatAccountType: this.wechatAccountType,
          linkMethod: this.linkMethod,
          isEditMode: false
        })
      } else if (order === 2) {
        this.$refs.OfficialSetupModal.open()
      } else if (order === 3) {
        this.$refs.syncDataModal.open(this.configuration, this.currentSetupStep > 3)
      } else if (order === 4) {
        this.$refs.ActivateModal.open()
      }
    },
    onConfigurationCreated (configuration) {
      this.configurationId = configuration.Id
      this.wechatAccountType = configuration
        ? configuration.Type : this.wechatAccountType
      this.configuration = configuration
    },
    async onSetupDone (wechatAccountType) {
      this.stopTimer = false
      this.wechatAccountType = wechatAccountType || this.wechatAccountType
      await this.getCurrentStep()
    },
    onModalClose () {
      this.$emit('close', { changed: this.changed })
      clearInterval(this.progressTimer)
    },
    onClose (event) {
      if (event && !this.changed) {
        this.changed = event.changed
      }
      this.stopTimer = false
    },
    async onSyncDataModalClose (data) {
      this.stopTimer = false
      if (data && !this.changed) {
        this.changed = data.changed
      }
      this.configuration.LinkWeChatFollowerTo = data.value
      if (!data.canNext) {
        return
      }
      await this.getCurrentStep()
    },
    onLinkMethodChanged (linkMethod) {
      this.$emit('link-method-changed', linkMethod)
    }
  }
}
</script>

<style lang="scss" scoped>
  .progress-modal {
    .scroll-content {
      height: 100%;
    }
    .slds-progress {
      padding: 16px 16px 32px;
    }
    .progress-item {
      &__title {
        font-weight: bold;
        line-height: 20px;
        margin-bottom: 2px;
      }
      &__button {
        height: 32px;
        margin-left: 15px;
      }
    }
    .slds-progress__item:last-child .progress-item {
      border-bottom: none;
    }
    .slds-progress__item_content.slds-grid.progress-item {
      align-items: flex-start;
    }
  }
</style>

<style lang="scss">
  .progress-modal .slds-modal .slds-modal__container {
    transform: none!important;
  }
</style>
