<template>
  <div
    class="slds-page-header slds-grid slds-grid_vertical wechat-account-configuration"
  >
    <div class="configuration-head">
      <div class="head-title">
        <span>WeChat Accounts</span>
        <button class="slds-button slds-button_neutral account-link-btn">
          <a
            class="account-link-label"
            href="javascript:void(0);"
            @click="onClickLinkWeChatAccount"
          >
            Link WeChat Account
          </a>
        </button>
      </div>
      <div
        class="item-count"
        v-if="!isLoading"
      >
        {{ configurations.length }} <span>{{ configurations.length > 1 ? 'items' : 'item' }}</span>
      </div>
    </div>
    <ld-spinner
      class="configuration-spinner"
      v-if="isLoading"
      container="true"
      color="brand"
    ></ld-spinner>
    <configuration-list
      class="configuration-list"
      v-if="!isLoading"
      :configurations="configurations"
      @launch-setup-wizard="onClickLaunchSetupWizard"
      @edit="onEdit"
      @sync="onSync"
      @delete-success="onDeleted"
    >
    </configuration-list>

    <sync-data-modal
      ref="syncDataModal"
      @close="onSyncDataModalClose"
    ></sync-data-modal>

    <select-account-type-modal
      ref="SelectAccountTypeModal"
      @select="onSelectAccountType($event)"
    ></select-account-type-modal>

    <select-link-method-modal
      ref="SelectLinkMethodModal"
      @select="onSelectLinkMethod($event)"
    ></select-link-method-modal>

    <progress-modal
      ref="ProgressModal"
      :type="selectedAccountType"
      :link-method="selectedLinkMethod"
      @link-method-changed="onLinkMethodChanged"
      @close="onProgressModalClose"
    ></progress-modal>

    <salesforce-setup
      ref="SalesforceSetup"
      @change="onSalesforceSetupChange()"
    ></salesforce-setup>
  </div>
</template>
<script>
import SalesforceSetup from './wechat_account_configuration/salesforce_setup'
import { WeChatAccountConfigurationService } from '../../service'
import ConfigurationList from './ConfigurationList.vue'
import SyncDataModal from './wechat_account_configuration/SyncDataModal.vue'
import SelectAccountTypeModal from './wechat_account_configuration/SelectAccountTypeModal.vue'
import SelectLinkMethodModal from './wechat_account_configuration/SelectLinkMethodModal.vue'
import ProgressModal from './wechat_account_configuration/ProgressModal.vue'
import { LdSpinner } from 'charket-ui/dist/ld-spinner.js'

export default {
  components: {
    ConfigurationList,
    LdSpinner,
    SyncDataModal,
    SelectAccountTypeModal,
    ProgressModal,
    SalesforceSetup,
    SelectLinkMethodModal
  },
  created () {
    this.fetchConfigurations()
  },
  mounted () {
    if (window.location.hash.includes('link-account=true')) {
      this.$refs.SelectLinkMethodModal.open()
      this.$router.push({ path: '/' })
    }
  },
  data () {
    return {
      configurations: [],
      isLoading: false,
      selectedAccountType: '',
      selectedLinkMethod: ''
    }
  },
  methods: {
    async fetchConfigurations () {
      this.isLoading = true
      try {
        this.configurations = await WeChatAccountConfigurationService.getConfigurations()
        this.configurations.forEach((configuration) => {
          if (configuration.IsSetupDone) {
            return
          }

          this.refreshProgressInfo(configuration)
        })
      } catch (error) {
        this.$handleError(error)
      } finally {
        this.isLoading = false
      }
    },
    async refreshProgressInfo (configuration) {
      try {
        await WeChatAccountConfigurationService.refreshProgressInfo(configuration)
      } catch (error) {
        if (error.name === 'RemoteAPIError') {
          const progressInfo = {
            TotalSteps: error.data.TotalSteps,
            CurrentSetupStep: error.data.CurrentSetupStep,
            IsSetupDone: error.data.IsSetupDone
          }
          configuration.setProgressInfo(progressInfo)
        } else {
          this.$handleError(error)
        }
      }
    },
    onDeleted () {
      this.fetchConfigurations()
    },
    onClickLinkWeChatAccount () {
      this.$refs.SelectLinkMethodModal.open()
    },
    onSelectAccountType (type) {
      this.selectedAccountType = type
      this.$refs.ProgressModal.open()
    },
    onSelectLinkMethod (linkMethod) {
      this.selectedLinkMethod = linkMethod
      this.$refs.SelectAccountTypeModal.open()
    },
    onClickLaunchSetupWizard (configuration) {
      this.selectedAccountType = configuration.Type
      this.selectedLinkMethod = configuration.LinkMethod
      this.$refs.ProgressModal.open(configuration)
    },
    onEdit (configuration) {
      this.$refs.SalesforceSetup.open({
        configurationId: configuration.Id,
        wechatAccountType: configuration.Type,
        isEditMode: true
      })
    },
    onSync (configuration) {
      this.$refs.syncDataModal.open(configuration, true)
    },
    onProgressModalClose (event) {
      if (event.changed) {
        this.fetchConfigurations()
      }
    },
    onSyncDataModalClose (data) {
      if (data.changed) {
        this.fetchConfigurations()
      }
    },
    onSalesforceSetupChange () {
      this.fetchConfigurations()
    },
    onLinkMethodChanged (linkMethod) {
      this.selectedLinkMethod = linkMethod
    }
  }
}
</script>
