<template>
  <div class="information">
    <div class="information__title">
      <p class="main-title"><strong>{{title}}</strong></p>
      <p class="sub-title" v-html="description"></p>
    </div>

    <img
      class="information__image"
      src="../../../../assets/images/send.png"
    />

    <div class="information__content">
      <div
        class="slds-form-element"
        v-if="ip"
      >
        <label class="slds-form-element__label">
          Salesforce Server IP Address
        </label>
        <div class="slds-form-element__control">
          <input
            class="slds-input"
            :value="ip"
            type="text"
            readonly="readonly"
          />
          <copy-button
            class="copy-to-clipboard-button"
            :message="ip"
            targetName="IP"
          >
            <icon
              name="copy_to_clipboard"
              icon-class-name="slds-button__icon"
            >
            </icon>
          </copy-button>
        </div>
      </div>
      <div class="slds-form-element">
        <label class="slds-form-element__label">
          URL
        </label>
        <div class="slds-form-element__control">
          <input
            class="slds-input"
            :value="url"
            type="text"
            readonly="readonly"
          />
          <copy-button
            class="copy-to-clipboard-button"
            :message="url"
            targetName="URL"
          >
            <icon
              name="copy_to_clipboard"
              icon-class-name="slds-button__icon"
            >
            </icon>
          </copy-button>
        </div>
      </div>
      <div class="slds-form-element">
        <label class="slds-form-element__label">
          Token
        </label>
        <div class="slds-form-element__control">
          <input
            class="slds-input"
            :value="token"
            type="text"
            readonly="readonly"
          />
          <copy-button
            class="copy-to-clipboard-button"
            :message="token"
            targetName="Token"
          >
            <icon
              name="copy_to_clipboard"
              icon-class-name="slds-button__icon"
            >
            </icon>
          </copy-button>
        </div>
      </div>
      <div class="copy-all-button__container">
        <ld-button
          v-clipboard:copy="allInformationToCopy"
          v-clipboard:success="onCopy"
          v-clipboard:error="onError"
          neutral
        >
          <icon
            class="copy-all-icon"
            name="copy_to_clipboard"
            icon-class-name="slds-button__icon"
          >
          </icon>
          Copy All
        </ld-button>
      </div>
    </div>
  </div>
</template>

<script>
import { LdButton } from 'charket-ui/dist/ld-button.js'

export default {
  name: 'Information',
  props: {
    ip: {
      type: String,
      default: ''
    },
    url: {
      type: String,
      required: true
    },
    token: {
      type: String,
      required: true
    },
    linkMethod: {
      type: String,
      default: 'Direct Integration'
    }
  },
  components: {
    LdButton
  },
  computed: {
    allInformationToCopy () {
      let toCopyContent = ''
      if (this.ip) {
        toCopyContent += `Salesforce server IP Address:\n${this.ip}\n`
      }
      toCopyContent += `URL: ${this.url}\nToken: ${this.token}`
      return toCopyContent
    },
    title () {
      const titleMap = {
        'Direct Integration': 'Send Salesforce Settings to WeChat Admin.',
        'Charket Social Hub': 'Save Salesforce Settings to Charket Social Hub'
      }

      return titleMap[this.linkMethod]
    },
    description () {
      const mail = '<a href="mailto:support@charket.com">support@charket.com</a>'
      const descriptionMap = {
        'Direct Integration': 'Send the following Salesforce settings to your WeChat admin for him/her to finish the setup on the WeChat end.',
        'Charket Social Hub': `Send the following Salesforce settings to ${mail} using the confidential mode if your email service supports it. Such information is considered confidential and no copy of it should be kept. The received email will be permanently deleted from our email server after the Charket support team finishes the setup in Charket Social Hub. Such Information will also be encrypted before being saved to the Charket Social Hub database.`
      }

      return descriptionMap[this.linkMethod]
    }
  },
  methods: {
    onCopy () {
      const content = `The information has been copied to your clipboard.`
      this.$bus.emit('success', { content, type: 'success' })
    },
    onError () {
      this.$bus.emit('error', { content: 'Copy failed.', type: 'error' })
    }
  }
}
</script>

