<template>
  <ld-modal
    class="select-modal"
    ref="Modal"
    title="Link WeChat Account"
  >
    <div slot="content">
      <div
        class="select-modal__loading"
        v-show="isLoading"
      >
        <ld-spinner color="brand"></ld-spinner>
      </div>

      <section
        class="slds-grid account-type-section"
        v-show="!isLoading"
      >
        <label class="slds-form-element__label account-type-section__label">Select Account Type</label>
        <fieldset class="slds-form-element">
          <div class="slds-form-element__control">
            <span
              class="slds-radio account-type-section__radio"
              v-for="(type, index) in accountTypes"
              :key="index"
              @click="onClickAccountType(type)"
            >
              <input
                type="radio"
                name="options"
                v-model="selectedAccountType"
                :value="type"
              >
              <label class="slds-radio__label">
                <span
                  class="slds-radio_faux"
                  :class="{'account-type-section__radio--selected': selectedAccountType === type}"
                ></span>
                <span class="slds-form-element__label account-type-section__radio--cursor">{{ type }}</span>
                <ld-tooltip>
                  <template>
                    <p v-text="getTooltipContent(type)"></p>
                  </template>
                  <template slot="activator">
                    <icon name="info" icon-class-name="info-icon"></icon>
                  </template>
                </ld-tooltip>
              </label>
            </span>
            </div>
        </fieldset>
      </section>
    </div>

    <div slot="footer">
      <ld-button
        :disabled="isNextButtonDisabled"
        @click="next()"
        brand
      >
        Next
      </ld-button>
    </div>
  </ld-modal>
</template>

<script>
import { LdButton } from 'charket-ui/dist/ld-button.js'
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdSpinner } from 'charket-ui/dist/ld-spinner.js'
import { LdTooltip } from 'charket-ui/dist/ld-tooltip.js'
import { WeChatAccountConfigurationService } from '../../../service'

export default {
  name: 'SelectAccountTypeModal',
  components: {
    LdButton,
    LdModal,
    LdSpinner,
    LdTooltip
  },
  data () {
    return {
      isLoading: false,
      accountTypes: [],
      selectedAccountType: ''
    }
  },
  computed: {
    isNextButtonDisabled () {
      return this.isLoading || !this.selectedAccountType
    }
  },
  methods: {
    async open () {
      this.isLoading = true
      this.reset()
      try {
        this.$refs.Modal.open()
        this.accountTypes = await WeChatAccountConfigurationService.getWeChatAccountTypes()
      } catch (error) {
        const errorMessage = this.$parseErrorMessage(error)
        this.$refs.Modal.showError(errorMessage)
      } finally {
        this.isLoading = false
      }
    },
    reset () {
      this.accountTypes = []
      this.selectedAccountType = 'Service Account'
    },
    getTooltipContent (type) {
      const typesToTooltipMapping = {
        'Service Account': '',
        'Subscription Account': '',
        'Mini Program': ''
      }
      return typesToTooltipMapping[type]
    },
    onClickAccountType (type) {
      this.selectedAccountType = type
    },
    next () {
      this.$refs.Modal.close()
      this.$emit('select', this.selectedAccountType)
    }
  }
}
</script>

