      margin-top: 16px;
      padding: 40px 0;
      &__label {
        margin-top: -35px;
        font-size: 0.8125rem;
        color: #080707;
        padding-right: 20px;
        padding-left: 35px;
      }

      &__radio {
        padding-bottom: 0.75rem;
        &--selected {
          border: 1px solid #1589ee;
        }
        &--cursor {
          cursor: pointer;
        }
      }
    }
  }
</style>

