<template>
  <ld-modal
    class="select-modal"
    ref="Modal"
    title="Link WeChat Account"
  >
    <div slot="content">
      <div
        class="select-modal__loading"
        v-if="isLoading"
      >
        <ld-spinner color="brand"></ld-spinner>
      </div>

      <section
        class="slds-grid account-type-section"
        v-else
      >
        <label class="slds-form-element__label account-type-section__label">Select Link Method</label>
        <fieldset class="slds-form-element">
          <div class="slds-form-element__control">
            <span
              class="slds-radio account-type-section__radio"
              v-for="(linkMethod, index) in linkMethods"
              :key="index"
              @click="onClickLinkMethod(linkMethod)"
            >
              <input
                type="radio"
                name="options"
                v-model="selectedLinkMethod"
                :value="linkMethod"
              >
              <label class="slds-radio__label">
                <span
                  class="slds-radio_faux"
                  :class="{'account-type-section__radio--selected': selectedLinkMethod === linkMethod}"
                ></span>
                <span class="slds-form-element__label account-type-section__radio--cursor">{{ linkMethod }}</span>
              </label>
            </span>
            </div>
        </fieldset>
      </section>
    </div>

    <div slot="footer">
      <ld-button
        :disabled="isNextButtonDisabled"
        @click="next()"
        brand
      >
        Next
      </ld-button>
    </div>
  </ld-modal>
</template>

<script>
import { LdButton } from 'charket-ui/dist/ld-button.js'
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdSpinner } from 'charket-ui/dist/ld-spinner.js'
import { WeChatAccountConfigurationService } from '../../../service'

export default {
  components: {
    LdButton,
    LdModal,
    LdSpinner
  },
  data () {
    return {
      isLoading: false,
      linkMethods: [],
      selectedLinkMethod: ''
    }
  },
  computed: {
    isNextButtonDisabled () {
      return this.isLoading || !this.selectedLinkMethod
    }
  },
  methods: {
    async open () {
      this.isLoading = true
      try {
        this.$refs.Modal.open()
        this.linkMethods = await WeChatAccountConfigurationService.getLinkMethods()
        this.selectedLinkMethod = this.linkMethods[0]
      } catch (error) {
        const errorMessage = this.$parseErrorMessage(error)
        this.$refs.Modal.showError(errorMessage)
      } finally {
        this.isLoading = false
      }
    },
    onClickLinkMethod (linkMethod) {
      this.selectedLinkMethod = linkMethod
    },
    next () {
      this.$refs.Modal.close()
      this.$emit('select', this.selectedLinkMethod)
    }
  }
}
</script>

