<template>
  <div>
    <lookup
      ref="Lookup"
      :ld-selected-item="selected"
      :ld-metadatas="metadata"
      @select="onSelectObject($event)"
      @remove="onSelectedObjectRemoved()"
    />
  </div>
</template>

<script>
import Lookup from '@/components/Lookup/Lookup.vue'

export default {
  components: {
    Lookup
  },
  props: {
    selected: {
      type: Object,
      default () {
        return null
      }
    }
  },
  data () {
    return {
      hasApexPermission: true,
      apexClassName: '',
      metadata: {
        name: 'ApexClass',
        label: 'Apex',
        labelPural: 'Apex Classes',
        icon: 'apex',
        linkField: {
          name: 'Name',
          label: 'Access Token Generator'
        },
        fields: []
      }
    }
  },
  watch: {
    selected (value) {
      if (this.isInputVisible && !value) {
        this.apexClassName = ''
      }
    }
  },
  created () {
    if (this.selected) {
      this.apexClassName = this.selected.Name
    }
  },
  methods: {
    onSelectObject (event) {
      this.$emit('select', event.name)
    },
    onSelectedObjectRemoved () {
      this.$emit('select', null)
    },
    onInput () {
      const apexClassName = this.apexClassName.trim()
      if (!apexClassName) {
        this.$emit('select', null)

        return
      }

      this.$emit('select', this.apexClassName)
    },
    reset () {
      if (this.$refs.Lookup) {
        this.$refs.Lookup.clear()
      }
      this.apexClassName = ''
    }
  }
}
</script>

