<template>
  <ld-modal
   ref="modal"
   :title="modalTitle"
   :persistent="isCancelDisabled"
  >
    <template slot="content">
      <div class="modal-content slds-grid slds-grid_align-center">
        <ld-spinner :container="true" color="brand" v-if="isLoading"></ld-spinner>
        <div v-if="!isLoading">
          <div class="slds-form-element__control slds-grid">
            <ld-checkbox-toggle
              label="Identify WeChat Users Using Union IDs"
              v-model="isIdentifyWeChatFollowersByUnionIdsEnabled"
            />
            <ld-tooltip>
              <p>If you connect to Salesforce multiple WeChat accounts Under the same WeChat Open Platform account, WeChat Followers sharing the same Union ID will be linked to The same Lead/Contact/Person Account record.</p>
              <icon
                slot="activator"
                name="info"
                icon-class-name="info-icon"
              ></icon>
            </ld-tooltip>
          </div>
          <div class="slds-form-element__control">
            <label class="slds-form-element__label slds-grid">
              Link WeChat Follower To
            </label>
            <ld-picklist
              class="picklist"
              :options="linkWeChatFollowerToOptions"
              :value="linkWeChatFollowerTo"
              @change="onLinkWeChatFollowerToChange"
              placeholder="--None--"
            >
            </ld-picklist>
          </div>
          <div
            class="slds-form-element__control"
            v-show="recordTypeOptions.length > 0"
          >
            <label class="slds-form-element__label slds-grid">
              <abbr
                class="slds-required"
                title="required"
              >* </abbr>Record Type
            </label>
            <ld-picklist
              ref="RecordType"
              class="picklist"
              :value="linkWeChatFollowerToRecordTypeId"
              :options="recordTypeOptions"
              @change="onRecordTypeChange"
              placeholder="--None--"
            >
            </ld-picklist>
          </div>
        </div>
      </div>
    </template>
    <template slot="footer">
      <ld-button
        neutral
        :disabled="isCancelDisabled"
        @click="close()"
      >
        Cancel
      </ld-button>
      <ld-button
        brand
        :disabled="isSaveDisabled"
        @click="save()"
      >
        Save
      </ld-button>
    </template>
  </ld-modal>
</template>
<script>
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { WeChatAccountConfigurationService } from '../../../service'
import { LdButton } from 'charket-ui/dist/ld-button.js'
import { LdPicklist } from 'charket-ui/dist/ld-picklist.js'
import { LdSpinner } from 'charket-ui/dist/ld-spinner.js'
import { LdCheckboxToggle } from 'charket-ui/dist/ld-checkbox-toggle.js'
import { LdTooltip } from 'charket-ui/dist/ld-tooltip.js'

export default {
  components: {
    LdModal,
    LdButton,
    LdPicklist,
    LdSpinner,
    LdCheckboxToggle,
    LdTooltip
  },
  data () {
    return {
      modalTitle: 'Change Link WeChat Follower Settings',
      isLoading: false,
      isSaving: false,
      linkWeChatFollowerToOptions: [],
      linkWeChatFollowerTo: 'None',
      sourceLinkWeChatFollowerTo: '',
      sourceLinkWeChatFollowerToRecordTypeId: '',
      sourceIsIdentifyWeChatFollowersByUnionIdsEnabled: true,
      hasChanged: false,
      configurationId: '',
      linkWeChatFollowerToRecordTypeId: '',
      recordTypeOptions: [],
      isIdentifyWeChatFollowersByUnionIdsEnabled: true
    }
  },
  computed: {
    isCancelDisabled () {
      return this.isSaving
    },
    isSaveDisabled () {
      return this.isLoading ||
        this.isSaving ||
        (this.recordTypeOptions.length > 0 &&
          !this.linkWeChatFollowerToRecordTypeId)
    }
  },
  methods: {
    async fetchData () {
      try {
        const configuration = await WeChatAccountConfigurationService.getConfiguration(this.configurationId)
        this.sourceLinkWeChatFollowerTo = configuration.LinkWeChatFollowerTo || 'None'
        this.sourceLinkWeChatFollowerToRecordTypeId = configuration.LinkWeChatFollowerToRecordTypeId || ''
        this.sourceIsIdentifyWeChatFollowersByUnionIdsEnabled = configuration.IsIdentifyWeChatFollowersByUnionIdsEnabled || false
        this.linkWeChatFollowerTo = this.sourceLinkWeChatFollowerTo
        this.linkWeChatFollowerToRecordTypeId = this.sourceLinkWeChatFollowerToRecordTypeId
        this.isIdentifyWeChatFollowersByUnionIdsEnabled = this.sourceIsIdentifyWeChatFollowersByUnionIdsEnabled
      } catch (e) {
        this.handleError(e)
      }
    },
    async open (id) {
      this.configurationId = id
      this.$refs.modal.open()
      this.isLoading = true
      await this.fetchData()
      this.setOptions()
    },
    close () {
      this.$emit('close', {
        changed: this.hasChanged,
        linkWeChatFollowerTo: this.hasChanged ? this.linkWeChatFollowerTo : this.sourceLinkWeChatFollowerTo

      })
      this.hasChanged = false
      this.$refs.modal.close()
    },
    async setOptions () {
      this.linkWeChatFollowerToOptions = await this.getOptions()
      this.getRecordTypeOptions()
      this.isLoading = false
    },
    getRecordTypeOptions () {
      const currentLinkWeChatFollowerToOption = this.linkWeChatFollowerToOptions.find(option => option.value === this.linkWeChatFollowerTo)
      this.recordTypeOptions = currentLinkWeChatFollowerToOption ? currentLinkWeChatFollowerToOption.recordTypes : []
    },
    async getOptions () {
      try {
        const options = await WeChatAccountConfigurationService.getLinkWeChatFollowerToOptions()
        return options
      } catch (e) {
        this.handleError(e)
      }
    },
    async save () {
      this.isSaving = true
      try {
        await WeChatAccountConfigurationService.updateLinkWeChatFollowerTo(this.configurationId, this.linkWeChatFollowerTo, this.linkWeChatFollowerToRecordTypeId, this.isIdentifyWeChatFollowersByUnionIdsEnabled)
        this.hasChanged = this.sourceLinkWeChatFollowerTo !== this.linkWeChatFollowerTo ||
          this.sourceLinkWeChatFollowerToRecordTypeId !== this.linkWeChatFollowerToRecordTypeId ||
          this.sourceIsIdentifyWeChatFollowersByUnionIdsEnabled !== this.isIdentifyWeChatFollowersByUnionIdsEnabled
        this.close()
      } catch (e) {
        this.handleError(e)
      } finally {
        this.isSaving = false
      }
    },
    onLinkWeChatFollowerToChange (option) {
      if (this.linkWeChatFollowerTo === option.value) {
        return
      }
      this.linkWeChatFollowerTo = option.value
      this.linkWeChatFollowerToRecordTypeId = ''
      this.$refs.RecordType.reset()
      this.recordTypeOptions = option.recordTypes
    },
    onRecordTypeChange (option) {
      this.linkWeChatFollowerToRecordTypeId = option.value
    },
    handleError (e) {
      const errorMessage = this.$parseErrorMessage(e)
      this.$refs.modal.showError(errorMessage)
    }
  }
}
</script>
<style lang="scss" scoped>
.modal-content {
  position: relative;
  padding: 32px 0;
  .picklist {
    text-align: left;
  }
  .info-icon {
    margin-left: 4px;
    fill: #706e6b;
    height: 11px;
    width: 11px;
    cursor: pointer;
  }
}
</style>
