<script>

import { LdModal } from 'charket-ui/dist/ld-modal'
import { LdSpinner } from 'charket-ui/dist/ld-spinner'
import { WeChatAccountConfigurationService, CharketSettingsService } from '../../../service'

export default {
  name: 'OfficialSetupModal',
  components: {
    LdModal,
    LdSpinner
  },
  props: {
    configurationId: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      serviceAccountSetupImage: require('../../../assets/images/service_acc_setup.png'),
      miniProgramSetupImage: require('../../../assets/images/mini_pro_setup.png'),
      configuration: {},
      sfdcServerIPAddress: '',
      isLoading: false,
      isWeChatLinked: false
    }
  },
  computed: {
    isMiniProgramAccount () {
      return this.configuration.Type === 'Mini Program'
    },
    isSFDCServerIPAddressVisible () {
      return this.configuration.IsIPWhitelistEnabled && !this.isMiniProgramAccount
    },
    serverSetupOrder () {
      return this.configuration.IsIPWhitelistEnabled ? 5 : 4
    },
    title () {
      return this.isWeChatLinked ? 'Set up WeChat' : 'Set up Charket Social Hub'
    }
  },
  methods: {
    async open () {
      this.$refs.modal.open()
      try {
        this.isLoading = true
        const configuration = await WeChatAccountConfigurationService.getConfiguration(this.configurationId)
        this.configuration = configuration
        this.isWeChatLinked = configuration.LinkMethod === 'Direct Integration'
        if (configuration.IsIPWhitelistEnabled) {
          this.sfdcServerIPAddress = await CharketSettingsService.getSFDCServerIPAddress()
        }
      } catch (err) {
        const errorMessage = this.$parseErrorMessage(err)
        this.$refs.modal.showError(errorMessage)
      } finally {
        this.isLoading = false
      }
    },
    onClickDone () {
      this.$emit('setup-done')
      this.$refs.modal.close()
    },
    onModalClose () {
      this.$emit('close')
    }
  }
}
</script>

