<template>
  <div class="slds-grid slds-grid_align-space entrance-page">
    <div class="entrance-section" :key="index" v-for="(entrance, index) in entrances">
      <div>
        <span class="slds-icon_container icon-span" :style="{background: entrance.color}">
          <icon :name="entrance.icon" :class="{'slds-icon--small': entrance.icon !== 'document'}" icon-class-name="slds-icon"></icon>
        </span>
      </div>
      <div class="entrance-title">{{ entrance.title }}</div>
      <div>{{ entrance.description }}</div>
      <div>
        <button class="slds-button slds-button_neutral" @click="entry(entrance.url)">{{ entrance.btn }}</button>
      </div>
    </div>

    <q-r-image-modal
      v-show="showQRImg"
      @close="hideQRImg"
    ></q-r-image-modal>
  </div>
</template>
<script>
import QRImageModal from './QRImageModal.vue'

export default {
  components: {
    QRImageModal
  },
  data () {
    return {
      entrances: [
        {
          color: '#FFB75D',
          icon: 'document',
          title: 'User Guide',
          description: 'Find documentation to help you use Charket',
          btn: 'Get Started',
          url: 'https://help.charket.com.cn'
        },
        {
          color: '#EA8288',
          icon: 'new_note',
          title: 'Release Notes',
          description: 'Learn about each Charket release',
          btn: 'Get Started',
          url: 'https://www.charket.com.cn/blog/tags/release/'
        },
        {
          color: '#4BCA81',
          icon: 'call',
          title: 'Support',
          description: 'Contact us on WeChat',
          btn: 'Contact Us',
          url: ''
        }
      ],
      showQRImg: false
    }
  },
  methods: {
    entry (url) {
      if (!url) {
        this.showQRImg = true
      } else {
        window.open(url)
      }
    },
    hideQRImg () {
      this.showQRImg = false
    }
  }
}
</script>
