<template>
  <ld-modal
    class="salesforce-setup"
    ref="modal"
    :width="640"
    @close="onModalClose()"
  >
    <div slot="header">
      <p class="header__title slds-text-heading_medium">{{ wechatAccountEditTitle }}</p>
    </div>
    <div
      class="slds-grid slds-grid_vertical modal-content"
      slot="content"
    >
      <ld-spinner
        v-show="isLoadingVisible"
        container="true"
        color="brand"
        medium
      ></ld-spinner>

      <div
        class="scroll-content overflow-hidden"
        :class="{'scroll-content-scrollable': !isSaving}"
        v-if="isFirstStep && !isLoading"
      >
        <div
          class="salesforce-setup__content slds-p-around_x-large"
        >
          <div class="slds-section">
            <h3 class="slds-section__title slds-theme_shade">
              <span class="slds-truncate">
                WeChat Account Information
              </span>
            </h3>
            <div class="section-content">
              <div class="slds-form slds-is-editing wechat-account-information">
                <div class="qr-code">
                  <image-chooser
                    :size-limit="imageSizeLimit"
                    :thumbnail-url="qrcodeImageUrl"
                    @choose="onClickChoose($event)"
                    thumbnail-title="QR Code"
                    :thumbnail-action="thumbnailAction"
                    preview-title="Upload QR Code"
                    size-limit-error="The maximum file size is 2 MB."
                    :cropper="true"
                  >
                  </image-chooser>
                </div>
                <div class="field-set">
                  <div class="slds-form-element">
                    <label class="slds-form-element__label" :for="getInputId('accountNameInputId')">
                      <abbr class="slds-required" title="required">* </abbr>Name</label>
                    <div class="slds-form-element__control">
                      <input
                        class="slds-input"
                        v-model="configuration.Name"
                        type="text"
                        :id="getInputId('accountNameInputId')"
                      />
                    </div>
                  </div>
                  <div class="slds-form-element">
                    <label
                      class="slds-form-element__label"
                      :for="getInputId('originInputId')"
                    >
                      <abbr
                        class="slds-required"
                        v-show="!isEditMode"
                        title="required"
                      >* </abbr>Original ID
                      <ld-tooltip v-if="configuration.LinkMethod === 'Direct Integration'">
                        <template>
                          <p>{{ originalIdTips }}</p>
                        </template>
                        <template slot="activator">
                          <icon name="info" icon-class-name="info-icon"></icon>
                        </template>
                      </ld-tooltip>
                    </label>
                    <div class="slds-form-element__control original-id">
                      <input
                        class="slds-input"
                        v-model="configuration.WeChatOriginId"
                        type="text"
                        :id="getInputId('originInputId')"
                        :readonly="isEditMode"
                      />
                    </div>
                  </div>
                  <div class="slds-form-element">
                    <label class="slds-form-element__label">
                      <abbr class="slds-required" title="required">* </abbr>Link Method
                    </label>
                    <div class="slds-form-element__control">
                      <ld-picklist
                        ref="LinkMethod"
                        :value="configuration.LinkMethod"
                        :options="linkMethodOptions"
                        @change="onLinkMethodChange($event)"
                        placeholder="--None--"
                      >
                      </ld-picklist>
                    </div>
                  </div>
                  <div class="slds-form-element">
                    <label class="slds-form-element__label">
                      <abbr class="slds-required" title="required">* </abbr>Type
                      <ld-tooltip>
                        <template>
                          <p> Service Account is </p>
                          <p> Subscription Account is </p>
                          <p> Mini Program is </p>
                        </template>
                        <template slot="activator">
                          <icon name="info" icon-class-name="info-icon"></icon>
                        </template>
                      </ld-tooltip>
                    </label>
                    <div class="slds-form-element__control">
                      <ld-picklist
                        ref="ConfigurationType"
                        :value="configuration.Type"
                        :options="wechatAccountTypeOptions"
                        @change="onWeChatAccountTypeChange($event)"
                        placeholder="--None--"
                      >
                      </ld-picklist>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="slds-section">
            <h3 class="slds-section__title slds-theme_shade">
              <span class="slds-truncate">
                Access Token Information
              </span>
            </h3>
            <div class="section-content">
              <div class="slds-form slds-is-editing access-token-information">
                <div
                  class="slds-grid slds-gutters_medium"
                  v-if="configuration.LinkMethod === 'Direct Integration'"
                >
                  <div class="slds-col slds-size_1-of-2">
                    <div class="slds-form-element">
                      <label class="slds-form-element__label">
                        <abbr class="slds-required" title="required">* </abbr>Access Token Provider
                      </label>
                      <div class="slds-form-element__control">
                        <ld-picklist
                          :value="configuration.AccessTokenGeneratorType"
                          :options="accessTokenGeneratorOptions"
                          @change="onAccessTokenGeneratorTypeChange($event)"
                        >
                        </ld-picklist>
                      </div>
                    </div>
                  </div>
                  <div
                    class="slds-col slds-size_1-of-2"
                    v-if="isAccessTokenGeneratorTypeCustomPull"
                  >
                    <div class="slds-form-element">
                      <label class="slds-form-element__label">
                        <abbr class="slds-required" title="required">* </abbr>Access Token Generator
                        <ld-tooltip>
                          <template>
                            <p>
                              An Apex class that implements the Charket.WeChatAccessTokenGenerator interface.
                              Charket uses this Apex class to get refreshed WeChat access tokens.
                            </p>
                          </template>
                          <template slot="activator">
                            <icon name="info" icon-class-name="info-icon"></icon>
                          </template>
                        </ld-tooltip>
                      </label>
                      <div class="slds-form-element__control">
                        <access-token-generator-lookup
                          :selected="getAccessTokenGeneratorLookupItem()"
                          @select="onCustomAccessTokenGeneratorSelect"
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div
                  class="slds-gutters_medium"
                  :class="{'slds-grid': configuration.LinkMethod !== 'Direct Integration'}"
                >
                  <div class="slds-col slds-size_1-of-2">
                    <div class="slds-form-element">
                      <label
                        class="slds-form-element__label"
                        :for="getInputId('appIdInputId')"
                      >
                        <abbr
                          class="slds-required"
                          v-show="!isEditMode"
                          title="required"
                        >* </abbr>App ID
                        <ld-tooltip v-if="configuration.LinkMethod === 'Direct Integration'">
                          <template>
                            <p>{{ appIdTips }}</p>
                          </template>
                          <template slot="activator">
                            <icon name="info" icon-class-name="info-icon"></icon>
                          </template>
                        </ld-tooltip>
                      </label>
                      <div class="slds-form-element__control app-id">
                        <input
                          class="slds-input"
                          v-model="configuration.AppId"
                          type="text"
                          :id="getInputId('appIdInputId')"
                          :readonly="isEditMode"
                        />
                      </div>
                    </div>
                  </div>
                  <div class="slds-col slds-size_1-of-2">
                    <div class="slds-form-element">
                      <label class="slds-form-element__label" :for="getInputId('appSecretInputId')">
                        <abbr
                          class="slds-required"
                          v-show="isAccessTokenGeneratorTypeWeChat"
                          title="required"
                        >* </abbr>App Secret
                        <ld-tooltip v-if="configuration.LinkMethod === 'Direct Integration'">
                          <template>
                            <p>{{ appSecretTips }}</p>
                          </template>
                          <template slot="activator">
                            <icon name="info" icon-class-name="info-icon"></icon>
                          </template>
                        </ld-tooltip>
                      </label>
                      <div class="slds-form-element__control">
                        <input
                          class="slds-input"
                          v-model="configuration.AppSecret"
                          type="text"
                          :id="getInputId('appSecretInputId')"
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="slds-section">
            <h3 class="slds-section__title slds-theme_shade">
              <span class="slds-truncate">
                WeChat Follower Settings
              </span>
            </h3>
            <div class="section-content">
              <div class="slds-form wechat-follower-sync-settings">
                <div class="slds-grid slds-gutters_medium">
                  <div class="slds-col slds-grid">
                    <ld-checkbox-toggle
                      label="Identify WeChat Users Using Union IDs"
                      v-model="configuration.IsIdentifyWeChatFollowersByUnionIdsEnabled"
                    />
                    <ld-tooltip>
                      <p>If you connect to Salesforce multiple WeChat accounts Under the same WeChat Open Platform account, WeChat Followers sharing the same Union ID will be linked to The same Lead/Contact/Person Account record.</p>
                      <icon
                        slot="activator"
                        name="info"
                        icon-class-name="info-icon"
                      ></icon>
                    </ld-tooltip>
                  </div>
                </div>
                <div class="slds-grid slds-gutters_medium">
                  <div class="slds-col slds-size_1-of-2">
                    <div class="slds-form-element slds-is-editing">
                      <label class="slds-form-element__label">
                        Link WeChat Follower To
                        <ld-tooltip>
                          <template>
                            <p>
                              A record of the selected object gets created whenever a WeChat user follows your WeChat account for the first time.
                            </p>
                          </template>
                          <template slot="activator">
                            <icon name="info" icon-class-name="info-icon"></icon>
                          </template>
                        </ld-tooltip>
                      </label>
                      <div class="slds-form-element__control">
                        <ld-picklist
                          :value="configuration.LinkWeChatFollowerTo"
                          :options="linkWeChatFollowerToOptions"
                          @change="onLinkWeChatFollowerToChange($event)"
                          placeholder="--None--"
                        >
                        </ld-picklist>
                      </div>
                    </div>
                  </div>
                </div>
                <div
                  class="slds-grid slds-gutters_medium"
                  v-show="recordTypeOptions.length > 0"
                >
                  <div class="slds-col slds-size_1-of-2">
                    <div class="slds-form-element slds-is-editing">
                      <label class="slds-form-element__label">
                        <abbr
                          class="slds-required"
                          title="required"
                        >* </abbr>Record Type
                      </label>
                      <div class="slds-form-element__control">
                        <ld-picklist
                          ref="RecordType"
                          :value="linkWeChatFollowerToRecordTypeId"
                          :options="recordTypeOptions"
                          @change="onRecordTypeChange($event)"
                          placeholder="--None--"
                        >
                        </ld-picklist>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="slds-section">
            <h3 class="slds-section__title slds-theme_shade">
              <span class="slds-truncate">
                Charket API Information
              </span>
            </h3>
            <div class="section-content">
              <div class="slds-form charket-api-information">
                <div class="slds-form-element">
                  <label class="slds-form-element__label">
                    <abbr
                      class="slds-required"
                      title="required"
                    >* </abbr>
                    Choose Your Site
                  </label>
                  <div class="slds-form-element__control">
                    <ld-picklist
                      :value="configuration.SiteId"
                      :options="siteOptions"
                      @change="onSiteChange($event)"
                      placeholder="--None--"
                    >
                    </ld-picklist>
                  </div>
                </div>
                <div class="slds-form-element">
                  <label class="slds-form-element__label" for="url">
                    <abbr
                      class="slds-required"
                      title="required"
                    >* </abbr>
                    URL
                  </label>
                  <div class="slds-form-element__control url">
                    <input
                      class="slds-input url-input"
                      v-model="configuration.CharketApiUrl"
                      type="text"
                      id="url"
                    />
                    <copy-button
                      class="copy-to-clipboard-button"
                      :message="configuration.CharketApiUrl"
                      targetName="URL"
                    >
                      <icon
                        name="copy_to_clipboard"
                        icon-class-name="slds-button__icon"
                      >
                      </icon>
                    </copy-button>
                  </div>
                </div>
                <div class="slds-form-element">
                  <label class="slds-form-element__label" :for="getInputId('tokenInputId')">
                    <abbr
                      class="slds-required"
                      title="required"
                    >* </abbr>
                    Token
                  </label>
                  <div class="slds-form-element__control token">
                    <div class="token-copy-input">
                      <input
                        class="slds-input"
                        v-model="configuration.Token"
                        type="text"
                        :id="getInputId('tokenInputId')"
                      />
                      <copy-button
                        class="copy-to-clipboard-button"
                        :message="configuration.Token"
                        targetName="Token"
                      >
                        <icon
                          name="copy_to_clipboard"
                          icon-class-name="slds-button__icon"
                        >
                        </icon>
                      </copy-button>
                    </div>
                    <ld-button
                      :disabled="isGeneratingToken"
                      @click="confirmGenerate()"
                      neutral
                    >
                      Generate
                    </ld-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class="scroll-content overflow-hidden"
        :class="{'scroll-content-scrollable': !isSaving}"
        v-if="isSecondStep"
      >
        <information
          :ip="sfdcServerIPAddress"
          :url="configuration.CharketApiUrl"
          :token="configuration.Token"
          :link-method="configuration.LinkMethod"
        >
        </information>
      </div>
    </div>

    <div
      class="salesforce-setup__footer"
      slot="footer"
    >
      <div
        class="next-button__container"
        v-if="isNextButtonVisible"
      >
        <ld-button
          :disabled="isNextButtonDisabled"
          @click="onClickNext()"
          brand
        >
          Next
        </ld-button>
      </div>
      <template v-if="isSecondStep">
        <ld-button
          @click="onClickBack()"
          neutral
        >
          Back
        </ld-button>
        <ld-button
          @click="onClickDone()"
          brand
        >
          Done
        </ld-button>
      </template>
      <div
        class="button-group"
        v-if="isEditMode"
      >
        <ld-button
          v-if="isEditMode"
          :disabled="isSaving"
          @click="onClickCancel()"
          neutral
        >
          Cancel
        </ld-button>
        <ld-button
          v-if="isSaveButtonVisible"
          :disabled="isSaveDisabled"
          @click="onClickSave()"
          brand
        >
          Save
        </ld-button>
      </div>
    </div>
  </ld-modal>
</template>

<script>
import { LdButton } from 'charket-ui/dist/ld-button.js'
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdPicklist } from 'charket-ui/dist/ld-picklist.js'
import { LdTooltip } from 'charket-ui/dist/ld-tooltip.js'
import { LdSpinner } from 'charket-ui/dist/ld-spinner.js'
import { ImageChooser } from 'charket-ui/dist/image-chooser.js'
import { LdCheckboxToggle } from 'charket-ui/dist/ld-checkbox-toggle.js'
import { WeChatAccountConfigurationService, CharketSettingsService } from '@/service'
import getGUID from '@/helpers/produce-GUID.js'
import AccessTokenGeneratorLookup from './AccessTokenGeneratorLookup.vue'
import Information from './Information.vue'

const DEFAULT_WECHAT_ACCOUNT_SETTINGS = {
  Id: null,
  Name: '',
  Type: '',
  AccessTokenGeneratorType: 'WeChat',
  AppId: '',
  AppSecret: '',
  CustomAccessTokenGenerator: '',
  LinkWeChatFollowerTo: 'None',
  QRCodeImageId: '',
  SiteId: '',
  Token: '',
  LinkMethod: '',
  WeChatOriginId: '',
  CharketApiUrl: '',
  IsIdentifyWeChatFollowersByUnionIdsEnabled: true,
  LinkWeChatFollowerToRecordTypeId: ''
}

export default {
  name: 'SalesforceSetup',
  components: {
    LdButton,
    LdModal,
    LdPicklist,
    LdTooltip,
    LdSpinner,
    ImageChooser,
    AccessTokenGeneratorLookup,
    Information,
    LdCheckboxToggle
  },
  data () {
    return {
      isEditMode: false,
      step: 1,
      isLoading: false,
      isGeneratingToken: false,
      configuration: {},
      qrcodeImage: {},
      sfdcServerIPAddress: '',
      imageSizeLimit: 2 * 1024 * 1024,
      allWechatAccountTypeOptions: [],
      linkMethodOptions: [],
      accessTokenGeneratorOptions: [{
        label: 'WeChat',
        value: 'WeChat'
      }, {
        label: 'Custom Push',
        value: 'Custom Push'
      }, {
        label: 'Custom Pull',
        value: 'Custom Pull'
      }],
      linkWeChatFollowerToOptions: this.fetchLinkWeChatFollowerToOptions,
      siteOptions: this.fetchSiteOptions,
      availableSites: [],
      isSaving: false,
      thumbnailAction: 'Uplaod',
      inputIds: {},
      recordTypeOptions: [],
      linkWeChatFollowerToRecordTypeId: ''
    }
  },
  computed: {
    wechatAccountEditTitle () {
      return this.isEditMode ? 'Edit WeChat Account' : 'Set up Salesforce'
    },
    wechatAccountTypeOptions () {
      if (this.isEditMode) {
        return this.allWechatAccountTypeOptions.filter((item) => {
          if (this.configuration.Type === 'Mini Program') {
            return item.value === 'Mini Program'
          } else {
            return item.value !== 'Mini Program'
          }
        })
      }

      return this.allWechatAccountTypeOptions
    },
    isMiniProgramWeChatAccount () {
      return this.configuration.Type === 'Mini Program'
    },
    qrcodeImageUrl () {
      return this.configuration.QRCodeImageId
        ? `/servlet/servlet.FileDownload?file=${this.configuration.QRCodeImageId}`
        : ''
    },
    originalIdTips () {
      const steps = this.isMiniProgramWeChatAccount ? '->->->' : '->->->'
      const originalIdTips = [
        'The Original ID is "ID" from WeChat. Your WeChat admin can find this ID at "',
        steps,
        '" in the WeChat admin portal.'
      ]

      return originalIdTips.join('')
    },
    appIdTips () {
      const appIdAlias = this.isMiniProgramWeChatAccount ? 'ID' : 'ID'
      const steps = this.isMiniProgramWeChatAccount ? '->->->ID' : '->->'
      const appIdTips = [
        'The App ID is "',
        appIdAlias,
        '", Your WeChat admin can find this ID at "',
        steps,
        '" in the WeChat admin portal.'
      ]

      return appIdTips.join('')
    },
    appSecretTips () {
      const steps = this.isMiniProgramWeChatAccount ? '->->->ID' : '->->'
      const appSecretTips = [
        'The App Secret is "", Your WeChat admin can find it at "',
        steps,
        '" in the WeChat admin portal.'
      ]

      return appSecretTips.join('')
    },
    isAccessTokenGeneratorTypeCustomPull () {
      return this.configuration.AccessTokenGeneratorType === 'Custom Pull'
    },
    isAccessTokenGeneratorTypeWeChat () {
      return this.configuration.AccessTokenGeneratorType === 'WeChat'
    },
    isCustomAccessTokenGeneratorValid () {
      return !!this.configuration.CustomAccessTokenGenerator ||
        !this.isAccessTokenGeneratorTypeCustomPull
    },
    isAppSecretValid () {
      return !!this.configuration.AppSecret || !this.isAccessTokenGeneratorTypeWeChat
    },
    isFormValid () {
      return !!this.configuration.Name &&
        !!this.configuration.Type &&
        !!this.configuration.WeChatOriginId &&
        !!this.configuration.AccessTokenGeneratorType &&
        this.isCustomAccessTokenGeneratorValid &&
        !!this.configuration.AppId &&
        this.isAppSecretValid &&
        !!this.configuration.SiteId &&
        !!this.configuration.CharketApiUrl &&
        !!this.configuration.Token &&
        (this.recordTypeOptions.length === 0 ||
          !!this.linkWeChatFollowerToRecordTypeId
        )
    },
    isFirstStep () {
      return this.step === 1
    },
    isSecondStep () {
      return this.step === 2
    },
    isBackButtonVisible () {
      return this.isSecondStep
    },
    isNextButtonVisible () {
      return !this.isEditMode && this.isFirstStep
    },
    isNextButtonDisabled () {
      return this.isSaving || !this.isFormValid
    },
    isSaveButtonVisible () {
      return this.isEditMode || this.isSecondStep
    },
    isSaveDisabled () {
      return !this.isFormValid || this.isSaving
    },
    isLoadingVisible () {
      return this.isLoading || (this.isFirstStep && this.isSaving)
    }
  },
  methods: {
    getAccessTokenGeneratorLookupItem () {
      if (this.configuration.CustomAccessTokenGenerator) {
        return { Name: this.configuration.CustomAccessTokenGenerator }
      } else {
        return null
      }
    },
    onCustomAccessTokenGeneratorSelect (item) {
      this.configuration.CustomAccessTokenGenerator = item
    },
    getInputId (name) {
      if (!this.inputIds[name]) {
        this.inputIds[name] = getGUID()
      }

      return this.inputIds[name]
    },
    async open ({
      configurationId,
      wechatAccountType,
      isEditMode,
      linkMethod
    }) {
      this.isLoading = true
      this.reset()
      this.isEditMode = isEditMode
      this.$refs.modal.open()
      if (configurationId) {
        try {
          await Promise.all([
            this.fetchData(configurationId),
            this.fetchWeChatAccountTypeOptions(),
            this.getLinkMethodsOptions(),
            this.getLinkWeChatFollowerToOptions(),
            this.getSiteOptions()
          ])
          this.thumbnailAction = this.qrcodeImageUrl ? 'Change' : 'Upload'
          this.getRecordTypeOptions()
          this.linkWeChatFollowerToRecordTypeId = this.configuration.LinkWeChatFollowerToRecordTypeId || ''
        } catch (e) {
          this.handleError(e)
        }
      } else {
        await Promise.all([
          this.fetchWeChatAccountTypeOptions(),
          this.getLinkMethodsOptions(),
          this.generateToken()
        ])
        this.configuration.Type = wechatAccountType
        this.configuration.LinkMethod = linkMethod
      }
      this.isLoading = false
    },
    async onClickChoose (qrcode) {
      this.thumbnailAction = 'Change'
      this.qrcodeImage = qrcode
    },
    async getLinkMethodsOptions () {
      this.linkMethodOptions = await this.fetchLinkMethodOptions()
    },
    async fetchLinkMethodOptions () {
      const linkMethods = await WeChatAccountConfigurationService.getLinkMethods()
      const options = linkMethods.map(linkMethod => {
        return {
          label: linkMethod,
          value: linkMethod
        }
      })

      return options
    },
    async fetchWeChatAccountTypeOptions () {
      const wechatAccountTypes = await WeChatAccountConfigurationService.getWeChatAccountTypes()
      this.allWechatAccountTypeOptions = wechatAccountTypes.map(wechatAccountType => {
        return {
          label: wechatAccountType,
          value: wechatAccountType
        }
      })
    },
    async getSiteOptions () {
      this.siteOptions = await this.fetchSiteOptions()
    },
    async fetchSiteOptions () {
      try {
        const sites = await WeChatAccountConfigurationService.getSites()
        this.availableSites = sites
        return sites.map(site => {
          return {
            label: site.Name,
            value: site.Id,
            data: {
              CharketApiUrl: site.CharketApiUrl
            }
          }
        })
      } catch (e) {
        this.handleError(e)
      }
    },
    getRecordTypeOptions () {
      const currentLinkWeChatFollowerToOption = this.linkWeChatFollowerToOptions.find(option => option.value === this.configuration.LinkWeChatFollowerTo)
      this.recordTypeOptions = currentLinkWeChatFollowerToOption ? currentLinkWeChatFollowerToOption.recordTypes : []
    },
    async getLinkWeChatFollowerToOptions () {
      this.linkWeChatFollowerToOptions = await this.fetchLinkWeChatFollowerToOptions()
    },
    async fetchLinkWeChatFollowerToOptions () {
      try {
        const linkWeChatFollowerToOptions = await WeChatAccountConfigurationService.getLinkWeChatFollowerToOptions()
        return linkWeChatFollowerToOptions
      } catch (e) {
        this.handleError(e)
      }
    },
    async fetchData (configurationId) {
      try {
        this.configuration = await WeChatAccountConfigurationService.getConfiguration(configurationId)
        if (this.configuration.IsIPWhitelistEnabled && !this.isEditMode) {
          await this.getSFDCServerIPAddress()
        }
      } catch (e) {
        this.handleError(e)
      }
    },
    onWeChatAccountTypeChange (option) {
      if (this.isMiniProgramWeChatAccount || option.value === 'Mini Program') {
        this.$refs.ConfigurationType.reset(true)
        return
      }
      this.configuration.Type = option.value
    },
    onLinkMethodChange (option) {
      if (this.configuration.LinkMethod === option.value) {
        return
      }
      this.configuration.AppSecret = ''
      this.configuration.LinkMethod = option.value
      this.$emit('link-method-changed', option.value)
    },
    onAccessTokenGeneratorTypeChange (option) {
      this.configuration.AccessTokenGeneratorType = option.value
      if (!this.isAccessTokenGeneratorTypeCustomPull) {
        this.configuration.CustomAccessTokenGenerator = ''
      }
    },
    onLinkWeChatFollowerToChange (option) {
      if (this.configuration.LinkWeChatFollowerTo === option.value) {
        return
      }
      this.configuration.LinkWeChatFollowerTo = option.value
      this.configuration.LinkWeChatFollowerToRecordTypeId = ''
      this.linkWeChatFollowerToRecordTypeId = ''
      this.$refs.RecordType.reset()
      this.recordTypeOptions = option.recordTypes
    },
    onSiteChange (option) {
      this.configuration.SiteId = option.value
      this.configuration.CharketApiUrl = option.data.CharketApiUrl
    },
    confirmGenerate () {
      const confirm = this.$confirm({
        title: 'Generate Token',
        content: 'Are you sure you want to generate a new token?',
        ok: 'Generate'
      })

      confirm.$on('ok', async () => {
        confirm.close()
        await this.generateToken()
      })
    },
    async generateToken () {
      this.isGeneratingToken = true
      try {
        this.configuration.Token = await WeChatAccountConfigurationService.generateToken()
      } catch (e) {
        this.handleError(e)
      }
      this.isGeneratingToken = false
    },
    async getSFDCServerIPAddress () {
      try {
        this.sfdcServerIPAddress = await CharketSettingsService.getSFDCServerIPAddress()
      } catch (e) {
        this.handleError(e)
      }
    },
    async onClickBack () {
      this.step = 1
      this.sfdcServerIPAddress = ''
      this.qrcodeImage = {}
      await Promise.all([
        this.getLinkWeChatFollowerToOptions(),
        this.getSiteOptions()
      ])
    },
    onClickCancel () {
      this.$refs.modal.close()
    },
    async onClickNext () {
      this.isSaving = true
      try {
        if (this.configuration.Id) {
          const configuration = await WeChatAccountConfigurationService.update(this.configuration)
          this.configuration.IsIPWhitelistEnabled = configuration.IsIPWhitelistEnabled
          this.$emit('create', this.configuration)
        } else {
          const configuration = await WeChatAccountConfigurationService.create(this.configuration)
          this.configuration.Id = configuration.Id
          this.configuration.IsIPWhitelistEnabled = configuration.IsIPWhitelistEnabled
          this.$emit('create', this.configuration)
        }
        if (this.qrcodeImage.content && !this.isSecondStep) {
          this.qrcodeImage.Id = await WeChatAccountConfigurationService.uploadQRCodeImage(
            this.configuration.Id,
            this.qrcodeImage
          )
        }
        if (this.configuration.IsIPWhitelistEnabled) {
          await this.getSFDCServerIPAddress()
        }
        this.step = 2
        this.configuration.QRCodeImageId = this.qrcodeImage.Id
        this.$emit('change')
        this.$emit('setup-done', this.configuration.Type)
      } catch (e) {
        this.handleError(e)
      }
      this.isSaving = false
    },
    async onClickSave () {
      this.isSaving = true
      try {
        await WeChatAccountConfigurationService.update(this.configuration)
        this.$emit('create', this.configuration)
        if (this.qrcodeImage.content && !this.isSecondStep) {
          this.qrcodeImage.Id = await WeChatAccountConfigurationService.uploadQRCodeImage(
            this.configuration.Id,
            this.qrcodeImage
          )
        }
        this.$bus.emit('success', {
          content: 'The WeChat Account was saved.',
          type: 'success'
        })
        this.$emit('change')
        this.$refs.modal.close()
      } catch (e) {
        this.handleError(e)
      }
      this.isSaving = false
    },
    onClickDone () {
      this.$refs.modal.close()
    },
    reset () {
      this.configuration = JSON.parse(JSON.stringify(DEFAULT_WECHAT_ACCOUNT_SETTINGS))
      this.linkMethodOptions = []
      this.siteOptions = this.fetchSiteOptions
      this.linkWeChatFollowerToOptions = this.fetchLinkWeChatFollowerToOptions
      this.isEditMode = false
      this.qrcodeImage = {}
      this.sfdcServerIPAddress = ''
      this.step = 1
      this.thumbnailAction = 'Upload'
      this.linkWeChatFollowerToRecordTypeId = this.configuration.LinkWeChatFollowerToRecordTypeId
      this.recordTypeOptions = []
    },
    onModalClose () {
      this.$emit('close')
    },
    handleError (e) {
      const errorMessage = this.$parseErrorMessage(e)
      this.$refs.modal.showError(errorMessage)
    },
    onRecordTypeChange (option) {
      this.configuration.LinkWeChatFollowerToRecordTypeId = option.value
      this.linkWeChatFollowerToRecordTypeId = option.value
    }
  }
}
</script>

