<template>
  <ld-modal
    class="sync-modal"
    :title="title"
    :persistent="isCancelDisabled"
    :width="640"
    ref="modal"
    @close="onModalClose()"
  >
    <div
      class="scroll-content slds-scrollable"
      slot="content"
      ref="content"
    >
      <div class="loading-area" v-if="isLoading">
        <ld-spinner :container="true" color="brand"></ld-spinner>
      </div>

      <div
        class="slds-p-around_medium slds-theme_success slds-text-heading_small modal-success-tip"
        v-if="successTip"
      >
        {{ successTip }}
      </div>

      <div
        class="modal-content-section"
        v-if="!isLoading"
      >
        <section class="sync-modal__info">
          <p class="title">Sync WeChat to Salesforce</p>
        </section>
        <p class="slds-m-bottom_x-large center">
          Select what WeChat data to sync to Salesforce.
        </p>
        <img
          class="sync-modal__image"
          src="../../../assets/images/sync.png"
        />
        <div class="sync-modal__choices">
          <div class="slds-form-element slds-m-left_medium sync-modal__choices__item slds-grid slds-grid_vertical-align-center">
            <ld-checkbox
              standalone
              v-model="syncDataConfig.syncFollower"
              :disabled="isCheckboxDisabled"
            >
              Each WeChat Follower will be linked with <strong>{{ linkedWith }}</strong>
            </ld-checkbox>

            <ld-tooltip>
              <template>
                <p>
                  Change Link WeChat Follower Settings
                </p>
              </template>
              <template slot="activator">
                <div @click="onClickLinkFollowerTo">
                  <icon
                    name="edit_icon"
                    class="edit-icon"
                    :class="{ disabled: syncDataStatus.isSyncingFollower }"
                    width="14px"
                    height="14px"
                  />
                </div>
              </template>
            </ld-tooltip>
            <div class="checkbox-loading-cover" v-if="syncDataStatus.isSyncingFollower">
              <div class="checkbox-loading">
                <ld-spinner x-small></ld-spinner>
              </div>
            </div>
          </div>

          <div class="slds-form-element slds-m-left_medium sync-modal__choices__item slds-grid slds-grid_vertical-align-center">
            <ld-checkbox
              standalone
              v-model="syncDataConfig.syncMenu"
              :disabled="isCheckboxDisabled"
            >
              WeChat Menu
            </ld-checkbox>

            <div class="checkbox-loading-cover" v-if="syncDataStatus.isSyncingMenu">
              <div class="checkbox-loading">
                <ld-spinner x-small></ld-spinner>
              </div>
            </div>
          </div>

          <div class="slds-form-element slds-m-left_medium sync-modal__choices__item slds-grid slds-grid_vertical-align-center">
            <ld-checkbox
              standalone
              v-model="syncDataConfig.syncAutoReplyRules"
              :disabled="isCheckboxDisabled"
            >
              WeChat Auto Replies
            </ld-checkbox>

            <div class="checkbox-loading-cover" v-if="syncDataStatus.isSyncingAutoReplyRules">
              <div class="checkbox-loading">
                <ld-spinner x-small></ld-spinner>
              </div>
            </div>
          </div>
        </div>

        <edit-link-follower-to-modal
          ref="linkFollowerToModal"
          @close="onLinkFollowerToModalClose"
        ></edit-link-follower-to-modal>
      </div>
    </div>

    <footer
      slot="footer"
    >
      <ld-button
        neutral
        :disabled="isCancelDisabled"
        @click="close()"
      >
        Close
      </ld-button>
      <ld-button
        brand
        v-if="skipButtonVisible"
        :disabled="isSkipDisabled"
        @click="skip()"
      >
        Skip
      </ld-button>
      <ld-button
        brand
        :disabled="isSubmitDisabled"
        @click="submit()"
      >
        Submit
      </ld-button>
    </footer>
  </ld-modal>
</template>

<script>
import { WeChatAccountConfigurationService } from '../../../service'
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdCheckbox } from 'charket-ui/dist/ld-checkbox.js'
import { LdSpinner } from 'charket-ui/dist/ld-spinner.js'
import { LdTooltip } from 'charket-ui/dist/ld-tooltip.js'
import { LdButton } from 'charket-ui/dist/ld-button.js'
import EditLinkFollowerToModal from './EditLinkFollowerToModal.vue'

const generateNameCombinationSentence = (names) => {
  if (names.length < 3) {
    return names.join(' and ')
  } else {
    return names.join(' and ').replace(' and ', ', ')
  }
}

export default {
  components: {
    LdModal,
    LdCheckbox,
    LdSpinner,
    LdTooltip,
    LdButton,
    EditLinkFollowerToModal
  },
  computed: {
    hasSyncOptionsSelected () {
      return this.syncDataConfig.syncFollower ||
        this.syncDataConfig.syncMenu ||
        this.syncDataConfig.syncAutoReplyRules
    },
    isSubmitDisabled () {
      return this.isLoading || this.isSubmitting || !this.hasSyncOptionsSelected || this.syncDataStatus.hasUncompletedJob
    },
    isSkipDisabled () {
      return this.isSubmitting || this.hasSyncOptionsSelected || this.syncDataStatus.hasUncompletedJob
    },
    isCancelDisabled () {
      return (this.isSubmitting || this.syncDataStatus.isSyncingMenu || this.syncDataStatus.isSyncingAutoReplyRules) && !this.isEdit
    },
    isCheckboxDisabled () {
      return this.syncDataStatus.isSyncingFollower || this.syncDataStatus.isSyncingMenu || this.syncDataStatus.isSyncingAutoReplyRules
    },
    linkedWith () {
      const value = this.linkWeChatFollowerTo ? this.linkWeChatFollowerTo : 'None'
      const map = {
        'None': '--None--',
        'Lead': 'Lead',
        'Contact': 'Contact',
        'Person Account': 'Person Account'
      }

      return map[value]
    },
    skipButtonVisible () {
      return !this.isLoading && !this.syncDataStatus.hasRunFirstSync
    }
  },
  watch: {
    'successTip' (value) {
      if (!value) {
        return null
      }
      setTimeout(() => {
        this.$refs.content.scrollTop = 0
      }, 0)
    }
  },
  data () {
    return {
      isLoading: false,
      title: 'Sync WeChat Data',
      syncDataConfig: {
        syncFollower: false,
        syncMenu: false,
        syncAutoReplyRules: false
      },
      isSubmitting: false,
      syncDataStatus: {
        isSyncingFollower: false,
        isSyncingMenu: false,
        isSyncingAutoReplyRules: false,
        hasRunFirstSync: false,
        hasUncompletedJob: false,
        checkedJob: []
      },
      checkSyncDataStatusJobTimer: null,
      linkWeChatFollowerTo: 'None',
      hasChanged: false,
      configuration: null,
      successTip: '',
      isEdit: false
    }
  },
  methods: {
    async open (configuration, isEdit = false) {
      this.isEdit = isEdit
      if (!configuration) {
        return
      }

      this.configuration = configuration
      this.$refs.modal.open()
      this.isLoading = true
      this.linkWeChatFollowerTo = this.configuration.LinkWeChatFollowerTo || 'None'
      await this.checkSyncDataJobStatus()
      this.isLoading = false
    },
    setCheckboxStatus () {
      const jobNameMapConfigKeys = {
        'Follower': 'syncFollower',
        'Menu': 'syncMenu',
        'AutoReplyRule': 'syncAutoReplyRules'
      }
      Object.keys(this.syncDataConfig).forEach(key => { this.syncDataConfig[key] = false })
      this.syncDataStatus.checkedJob.forEach((job) => {
        if (jobNameMapConfigKeys[job]) {
          this.syncDataConfig[jobNameMapConfigKeys[job]] = true
        }
      })
    },
    close () {
      this.$refs.modal.close()
    },
    async skip () {
      await this.startSyncData({
        syncFollower: false,
        syncMenu: false,
        syncAutoReplyRules: false
      }, true)
      this.close()
      this.$emit('change')
    },
    submit () {
      this.successTip = ''
      this.$refs.modal.clearError()
      this.startSyncData(this.syncDataConfig)
    },
    async startSyncData (syncDataConfig, isSkipped = false) {
      this.isSubmitting = true
      try {
        await WeChatAccountConfigurationService.syncWeChatData(this.configuration.Id, syncDataConfig)

        await this.handleSubmitSuccess(isSkipped)
      } catch (e) {
        this.handleError(e)
      } finally {
        this.isSubmitting = false
      }
    },
    async handleSubmitSuccess (isSkipped) {
      this.successTip = isSkipped ? '' : 'The sync request was submitted.'
      this.syncDataStatus.isSyncingFollower = this.syncDataConfig.syncFollower
      this.syncDataStatus.isSyncingMenu = this.syncDataConfig.syncMenu
      this.syncDataStatus.isSyncingAutoReplyRules = this.syncDataConfig.syncAutoReplyRules
      await this.checkSyncDataJobStatus()
      this.$emit('setup-done')
    },
    async checkSyncDataJobStatus () {
      this.syncDataStatus = await this.getSyncDataJobStatus()
      this.successTip = this.generateSuccessTip()
      this.setCheckboxStatus()

      if (this.syncDataStatus.hasUncompletedJob) {
        this.lauchCheckSyncDataStatusJobTimer()
      }
    },
    getCheckedJobs () {
      const configMapJob = {
        syncFollower: 'Follower',
        syncMenu: 'Menu',
        syncAutoReplyRules: 'AutoReplyRule'
      }
      const checkedJobFromCheckBox = Object.keys(this.syncDataConfig).filter(key => this.syncDataConfig[key]).map(key => configMapJob[key])

      if (checkedJobFromCheckBox.length > 0) {
        return checkedJobFromCheckBox
      } else {
        return this.syncDataStatus.checkedJob
      }
    },
    generateSuccessTip () {
      const checkedJob = this.getCheckedJobs()
      if (checkedJob.length === 0) {
        return ''
      }

      const jobMapStatus = {
        Follower: 'isSyncingFollower',
        Menu: 'isSyncingMenu',
        AutoReplyRule: 'isSyncingAutoReplyRules'
      }
      const jobMapDataName = {
        Follower: 'WeChat Follower',
        Menu: 'WeChat Menu',
        AutoReplyRule: 'WeChat Auto Reply Rules'
      }

      const finishedJobs = checkedJob.filter(job => !this.syncDataStatus[jobMapStatus[job]])
      const unFinishedJobs = checkedJob.filter(job => this.syncDataStatus[jobMapStatus[job]])

      const finishedNameCombinationSentence = generateNameCombinationSentence(finishedJobs.map(job => {
        return jobMapDataName[job]
      }))
      const unFinishedNameCombinationSentence = generateNameCombinationSentence(unFinishedJobs.map(job => {
        return jobMapDataName[job]
      }))

      const finishedBeVerb = finishedJobs.length > 1 ? 'were' : 'was'
      const unFinishedBeVerb = unFinishedJobs.length > 1 ? 'are' : 'is'

      const finishedSentence = finishedNameCombinationSentence ? `The ${finishedNameCombinationSentence} ${finishedBeVerb} synced.\n` : ''
      const unFinishedSentence = unFinishedNameCombinationSentence ? `The sync of ${unFinishedNameCombinationSentence} ${unFinishedBeVerb} still in progress.` : ''

      const nextStepSentence = this.syncDataStatus.isSyncingMenu || this.syncDataStatus.isSyncingAutoReplyRules || this.isEdit ? '' : ' It\'s okay now to click the Close button to go to the next step.'

      return `${finishedSentence}${unFinishedSentence}${nextStepSentence}`
    },
    async getSyncDataJobStatus () {
      const syncDataStatus = {
        isSyncingFollower: false,
        isSyncingMenu: false,
        isSyncingAutoReplyRules: false,
        hasRunFirstSync: false,
        hasUncompletedJob: false,
        checkedJob: []
      }

      try {
        const result = await WeChatAccountConfigurationService.checkSyncDataJobStatus(this.configuration.Id)
        syncDataStatus.hasRunFirstSync = !result.IsFirstSynchronizing
        if (result.IsFirstSynchronizing) {
          syncDataStatus.isSyncingFollower = false
          syncDataStatus.isSyncingMenu = false
          syncDataStatus.isSyncingAutoReplyRules = false
        } else {
          result.SyncDataJobStatus.forEach(jobStatus => {
            syncDataStatus.checkedJob.push(jobStatus.Name)
            if (jobStatus.Name === 'Follower') {
              syncDataStatus.isSyncingFollower = !jobStatus.IsCompleted
            } else if (jobStatus.Name === 'Menu') {
              syncDataStatus.isSyncingMenu = !jobStatus.IsCompleted
            } else if (jobStatus.Name === 'AutoReplyRule') {
              syncDataStatus.isSyncingAutoReplyRules = !jobStatus.IsCompleted
            }
          })
          syncDataStatus.hasUncompletedJob = result.SyncDataJobStatus.some(jobStatus => !jobStatus.IsCompleted)
        }
      } catch (e) {
        this.handleError(e)
      }

      return syncDataStatus
    },
    lauchCheckSyncDataStatusJobTimer () {
      this.checkSyncDataStatusJobTimer = setTimeout(() => {
        this.checkSyncDataJobStatus()
      }, 30000)
    },
    onClickLinkFollowerTo () {
      const configurationId = this.configuration ? this.configuration.Id : ''
      this.$refs.linkFollowerToModal.open(configurationId)
    },
    onModalClose () {
      this.successTip = ''
      if (this.checkSyncDataStatusJobTimer) {
        clearTimeout(this.checkSyncDataStatusJobTimer)
        this.checkSyncDataStatusJobTimer = null
      }
      this.syncDataConfig = {
        syncFollower: false,
        syncMenu: false,
        syncAutoReplyRules: false
      }
      this.$emit('close', { changed: this.hasChanged, value: this.linkWeChatFollowerTo, canNext: this.isSyncingButCanNext() })
      this.hasChanged = false
    },
    isSyncingButCanNext () {
      return this.syncDataStatus.hasUncompletedJob &&
      !this.syncDataStatus.isSyncingAutoReplyRules &&
      !this.syncDataStatus.isisSyncingMenu &&
      this.syncDataStatus.isSyncingFollower
    },
    onLinkFollowerToModalClose (data) {
      this.hasChanged = data.changed
      this.linkWeChatFollowerTo = data.linkWeChatFollowerTo
    },
    handleError (e) {
      const errorMessage = this.$parseErrorMessage(e)
      this.$refs.modal.showError(errorMessage)
    }
  }
}
</script>

<style lang="scss" scoped>
