    text-align: center;
  }

  .loading-area {
    position: relative;
    padding: 32px;
  }

  .sync-modal {
    .center {
      text-align: center;
      margin-top: 16px;
    }

    &__image {
      margin-bottom: 32px;
      width: 289px;
      height: 134px;
    }

    &__info {
      text-align: center;
      .title {
        font-size: 24px;
        font-weight: bold;
        line-height: 24px;
      }
    }

    &__choices {
      margin-left: 70px;
      &__item {
        height: 28px;
      }
      &__item:first-child {
        margin-top: -6px;
      }
      &__label {
        cursor: pointer;
      }
    }

    .type-picklist {
      display: inline-block;
      width: 120px;
    }
  }

  .checkbox-loading-cover {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    .checkbox-loading {
      margin-left: -8px;
      width: 32px;
      height: 100%;
      background-color: #fff;
    }
  }

  .edit-icon {
    margin-left: -3px;
    fill: #706e6b;
    &:hover {
      fill: #004487;
    }
    cursor: pointer;

    &.disabled {
      fill: #c9c7c5;
    }
  }

  .modal-success-tip {
    border-radius: 4px;
