<template>
  <div class="slds-grid slds-grid_vertical">
    <!-- todo: replace with ld-table component -->
    <table class="slds-table slds-table_cell-buffer slds-table_bordered configuration-list">
      <thead>
        <tr class="slds-line-height_reset list-header-tab">
          <th class="first-list-header" scope="col">
            <div class="slds-truncate" title="Name">Name</div>
          </th>
          <th scope="col">
            <div class="slds-truncate" title="Type">Type</div>
          </th>
          <th scope="col">
            <div class="slds-truncate" title="Link WeChat Follower To">Link WeChat Follower To</div>
          </th>
          <th scope="col">
            <div class="slds-truncate" title="Setup Status">Setup Progress</div>
          </th>
          <th scope="col">
            <div class="slds-truncate" title="Last Modified By">Last Modified By</div>
          </th>
          <th scope="col" style="width: 3rem;">
          </th>
        </tr>
      </thead>
      <tbody v-if="configurations.length > 0">
        <tr
          class="slds-hint-parent"
          v-for="(configuration, index) in configurations"
          :key="index"
        >
          <th data-label="Name" class="first-list-header" scope="row">
            <div class="slds-truncate">
              <a
                :href="'/' + configuration.Id"
                :title="configuration.Name"
                target="_blank"
              >
                {{ configuration.Name }}
              </a>
            </div>
          </th>
          <td data-label="Type">
            <div class="slds-truncate">{{ configuration.Type }}</div>
          </td>
          <td data-label="LinkWeChatFollowerTo">
            <div class="slds-truncate">
              {{ configuration.LinkWeChatFollowerTo || 'None' }}
            </div>
          </td>
          <td data-label="Setup Status">
            <div class="slds-truncate">
              <ld-progress-ring
                v-if="configuration.Progress >= 0"
                :percent="configuration.Progress"
              >
              </ld-progress-ring>
              <a
                v-if="configuration.Progress >= 0 && configuration.Progress < 100"
                class="slds-m-left_x-small"
                href="javascript:void(0);"
                @click="onClickLaunchSetupWizard(configuration)"
              >
                Launch Setup Wizard
              </a>
              <div
                v-if="configuration.Progress === -1"
                class="setup-status-loading"
              >
                <ld-spinner x-small color="brand"></ld-spinner>
              </div>
            </div>
          </td>
          <td data-label="LastModifiedBy">
            <div
              class="slds-truncate"
              :title="configuration.lastModifiedInfo"
            >
              {{ configuration.lastModifiedInfo }}
            </div>
          </td>
          <td data-label="Action">
            <ld-action-menu
              :items="getMenuItems(configuration)"
              @click="onClickAction"
            >
            </ld-action-menu>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="no-records" v-if="configurations.length === 0">
      <div class="slds-text-body_regular">No Records</div>
    </div>
    <delete-modal
      ref="deleteModal"
      @success="onDeleteSuccess"
    ></delete-modal>
  </div>
</template>
<script>
import DeleteModal from './DeleteModal.vue'
import { LdActionMenu } from 'charket-ui/dist/ld-action-menu.js'
import { LdProgressRing } from 'charket-ui/dist/ld-progress-ring.js'
import { LdSpinner } from 'charket-ui/dist/ld-spinner.js'

export default {
  components: {
    LdActionMenu,
    LdProgressRing,
    LdSpinner,
    DeleteModal
  },
  props: {
    configurations: {
      type: Array,
      required: true
    }
  },
  methods: {
    getMenuItems (configuration) {
      const actions = [
        { label: 'Delete', value: 'delete', data: configuration }
      ]

      if (configuration.IsSetupDone) {
        if (configuration.Type !== 'Mini Program') {
          actions.unshift({ label: 'Sync Data', value: 'sync', data: configuration })
        }
        actions.unshift({ label: 'Edit', value: 'edit', data: configuration })
      }

      return actions
    },
    onClickAction (action) {
      if (action.value === 'edit') {
        this.$emit('edit', action.data)
      } else if (action.value === 'delete') {
        this.openDeleteModal(action.data)
      } else if (action.value === 'sync') {
        this.$emit('sync', action.data)
      }
    },
    onClickLaunchSetupWizard (configuration) {
      this.$emit('launch-setup-wizard', configuration)
    },
    openDeleteModal (configuration) {
      this.$refs.deleteModal.open(configuration)
    },
    onDeleteSuccess () {
      this.$emit('delete-success')
    }
  }
}
</script>
<style lang="scss" scoped>
.no-records {
  display: flex;
  background-color: #fafaf9;
  justify-content: space-around;
  align-items: center;
  flex-grow: 1;
  flex-shrink: 0;
  margin-bottom: -50px;
}
.configuration-list {
  table-layout: fixed;
  .list-header-tab {
    height: 32px;
  }
  .first-list-header {
    padding-left: 20px;
  }
}
.setup-status-loading {
  width: 28px;
  height: 28px;
}
</style>
